<template>
    <div class="dlu">
        <div class="header" >
            <router-link to="/my">
                <img src="../assets/img/xtb/ic_login_close.png">
            </router-link>
            <span class="dl"></span>
            <table></table>
            <router-link to="/login">
                <span class="zc"></span>
            </router-link>
        </div>
        <div class="put" >
            <mt-field v-model="tel" label="" placeholder=""></mt-field>
            <mt-field label="" placeholder=""></mt-field>
            <mt-field v-model="pwd" type="password" class="mm" label="" placeholder=""></mt-field>
            <div class="yzm1">
                <p></p>
            </div>
            <button class="btn-dl" @click="reg()"></button>
            <p class="xieyi"><a></a><a></a></p>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            tel:"",
            pwd:""
        }
    },  
    methods: {
        reg(){
           var t=this.tel;
           var p=this.pwd; 
           //      1 3~9 9 13
            var treg = /^[1]([3-9])[0-9]{9}$/;
            //        3~8
            var preg = /^[0-9]{3,8}$/;
            // 3:  
            if (!treg.test(t)) {
                //2.1: 
                this.$toast("");
                return;
            }
            // 4:    
            if (!preg.test(p)) {
                //2.2 
                this.$toast("");
                return;
            }
            var url="http://127.0.0.1:3000/reg?name="+t+"&pwd="+p;
            this.axios.post(url).then(result=>{
                console.log(result)
                var code=result.data.code;
                if(code==-2){
                    this.$toast(",")
                }else if(code==1){
                    this.$toast("")
                    this.$router.push("/login")
                }
            })
        }
        
    },
}
</script>
