<template>
  <div class="dlu">
    <div class="header">
      <router-link to="/my">
        <img src="../assets/img/xtb/ic_login_close.png">
      </router-link>
      <span class="dl"></span>
      <table></table>
      <router-link to="/registe">
        <span class="zc"></span>
      </router-link>
    </div>
    <div class="dxin1">
      <div class="dxin" @click="dxin1()" :class="dx==true?'':'huoqu'"></div>
      <div class="dxin" @click="dxin2()" :class="zc==true?'':'huoqu'"></div>
      <div class="qfd"></div>
    </div>
    <div class="put" :class="zc==true?'':'out'">
      <mt-field label="" placeholder=""></mt-field>
      <mt-field label="" placeholder=""></mt-field>
      <div class="yzm">
        <p></p>
      </div>
      <button class="btn-dl"></button>
    </div>
    <div class="put" :class="dx==true?'':'out'">
      <mt-field label="" placeholder="" type="tel" v-model="tel"></mt-field>
      <mt-field class="mm" label="" placeholder="" type="password" v-model="pwd"></mt-field>
      <button :class="'btn-dl '+(value?'lt':'')" @click="btnLogin"></button>
      <a href>?</a>
    </div>
  </div>
</template>
<script>
import { setTimeout } from "timers";
export default {
  data() {
    return {
      tel: "", //
      pwd: "", //
      dx: true,
      zc: false,
      value:false
    };
  },
  created() {
    this.$store.state.tel = this.tel;
  },
  watch: {
    tel: function(newv, oldv) {
      if(newv!==oldv){
          this.value = this.pwd != ""
      }
    },
    pwd:function(newv,oldv){
        if(newv!==oldv){
          this.value = this.tel != ""
      }
    }
  },
  methods: {
    dxin1() {
      this.dx = false;
      this.zc = true;
    },
    dxin2() {
      this.dx = true;
      this.zc = false;
    },
    btnLogin() {
      //
      //ajax
      //
      console.log(11);
      // 1:
      var t = this.tel;
      var p = this.pwd;
      // console.log("2:" + t + ":" + p);
      // 2:
      //      1 3~9 9 13
      var treg = /^[1]([3-9])[0-9]{9}$/;
      //        3~8
      var preg = /^[0-9]{3,8}$/;
      // 3:  
      if (!treg.test(t)) {
        //2.1: 
        this.$toast("");
        return;
      }
      // 4:    
      if (!preg.test(p)) {
        //2.2 
        this.$toast("");
        return;
      }
      // 5:ajax http://127.0.0.1:300/login
      var url = "http://127.0.0.1:3000/login?name=" + t + "&pwd=" + p;
      this.axios.get(url).then(result => {
         console.log(result);
        // console.log("");
        if (result.data.code == 1) {
          this.$toast("");
          setTimeout(function() {
            location.href = "http://127.0.0.1:8080/my";
          }, 1000);
          var tid=result.data.res[0].id;
          var ses=window.sessionStorage;
          ses.setItem("data",tid);
          var ntid=window.sessionStorage.data;
        } else {
          this.$toast("");
        }
      });
      // 6:
      // 7: 
    }
  }
};
</script>
<style scoped>
.huoqu {
  color: #333;
  border-bottom: 2px solid #f83b3f;
}
.out {
  display: none;
}
.lt{
    background-color: red;
}
</style>
